.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Navigation Bar */
.navbar {
  display: flex;
  align-items: center;
  background: #232946;
  color: #fff;
  padding: 18px 32px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
}
.nav-home {
  background: #eebbc3;
  color: #232946;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  padding: 8px 20px;
  margin-right: 24px;
  cursor: pointer;
  transition: background 0.2s;
}
.nav-home:hover {
  background: #ffd6e0;
}
.nav-title {
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 1px;
}

/* Vertical Menu */
.menu-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  gap: 28px;
}
.menu-btn {
  background: linear-gradient(90deg, #eebbc3 0%, #b8c1ec 100%);
  color: #232946;
  border: none;
  border-radius: 16px;
  font-size: 1.3rem;
  font-weight: 600;
  padding: 18px 60px;
  box-shadow: 0 4px 16px rgba(35,41,70,0.08);
  cursor: pointer;
  text-decoration: none;
  transition: background 0.2s, transform 0.1s;
  width: 340px; /* Set a fixed width for all buttons */
  max-width: 90vw;
  text-align: center;
  display: flex;
  justify-content: center;
}
.menu-btn:hover {
  background: linear-gradient(90deg, #ffd6e0 0%, #b8c1ec 100%);
  transform: translateY(-2px) scale(1.04);
}

/* Omok Modal Popup */
.omok-modal {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.omok-modal-content {
  background: #fff;
  border-radius: 18px;
  padding: 48px 64px;
  box-shadow: 0 8px 32px rgba(35,41,70,0.18);
  font-size: 2.2rem;
  font-weight: bold;
  color: #232946;
  text-align: center;
}
.omok-modal button {
  margin-top: 32px;
  font-size: 1.1rem;
  padding: 12px 36px;
  border-radius: 8px;
  border: none;
  background: #eebbc3;
  color: #232946;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}
.omok-modal button:hover {
  background: #ffd6e0;
}

/* Game Page Body */
.game-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  gap: 32px;
}
.game-desc {
  font-size: 1.2rem;
  color: #232946;
  background: #f4f6fb;
  border-radius: 12px;
  padding: 24px 32px;
  box-shadow: 0 2px 8px rgba(35,41,70,0.06);
  max-width: 480px;
  text-align: center;
}
.start-btn {
  background: linear-gradient(90deg, #b8c1ec 0%, #eebbc3 100%);
  color: #232946;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: bold;
  padding: 14px 40px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(35,41,70,0.08);
  transition: background 0.2s, transform 0.1s;
}
.start-btn:hover {
  background: linear-gradient(90deg, #b8c1ec 0%, #ffd6e0 100%);
  transform: translateY(-2px) scale(1.04);
}

body {
  background: #f4f6fb;
}
